type TApiObject = { api: string; objName: string };
type TReduxState = {
	root: {
		admin: {
			formNameMaster: IFormNameMaster[];
		};
		formData: {
			permitData: {};
			userData: {};
			comment: {};
			history: {};
			success: {};
			printcontent: any;
			fileCategories: any;
			files: any;
		};
		ui: {
			loading: boolean;
			errors: {};
		};
		general: {
			printcontent: any;
		};
	};
};
type TProps = {
	// parentProps: { location: { pathname: string } };
	api: TApiObject[];
	// checkPreviousFiles?: boolean;
	// fetchFiles?: boolean;
	// localData?: {};
	// history: { push: (url: string) => void };
	// // Actions
	// getFormContainerData: (api: TApiObject[], prepareData: (data: any) => {}, formUtility: FormUtility, localData: {}, fetchFile: boolean) => void;
	// Redux state
	permitData: {};
	userData: {};
	formData: {};
	comment: {};
	formHistory: {};
	adminData: {
		formNameMaster: IFormNameMaster[];
	};
	loading: boolean;
	errors: {};
	success: {};
	printcontent: any;
	fileCategories: any;
	files: any;
	getPermitAndUserData: any;
	getFormContainerData: any;
	getAdminDataByUrl: any;
	postFormDataByUrl: any;
	postFormDataWithFileByUrl: any;
	render: ({}: any) => Element;
};

type TState = {
	allLoaded: boolean;
	cFormMaster: IFormNameMaster | null;
};

class DynamicFormContaner extends React.Component<TProps, TState> {
	formRef: RefObject<HTMLDivElement> = React.createRef();
	constructor(props: TProps) {
		super(props);
		this.state = {
			allLoaded: false,
			cFormMaster: null,
		};
	}

	componentDidMount() {
		this.props.getAdminDataByUrl(this.props.api);
	}

	componentDidUpdate(prevProps: TProps) {
		const { adminData } = this.props;
		if (this.props.adminData !== prevProps.adminData) {
			const dataNames = this.props.api;
			let hasLoaded: boolean[] = [];
			let data: any = {};

			dataNames.forEach((obj, index) => {
				//@ts-ignore
				if (!isEmpty(this.props.adminData[obj.objName]) || Array.isArray(this.props.adminData[obj.objName])) {
					hasLoaded[index] = true;
					Object.assign(data, {
						//@ts-ignore
						[obj.objName]: this.props.adminData[obj.objName],
					});
				} else {
					hasLoaded[index] = false;
				}
			});

			const cFormMaster = adminData && adminData.formNameMaster && adminData.formNameMaster.find((row) => row.id === 5);
			this.setState({ allLoaded: hasLoaded.reduce((loaded, next) => loaded && next, true), cFormMaster: cFormMaster || null });
		}

		if (this.props.success !== prevProps.success) {
			// this.props.getAfterUpdateAdminData(this.props.updateApi);
		}
	}
	render() {
		const { cFormMaster } = this.state;
		if (this.state.allLoaded) {
			return (
				<div>
					<DynamicActionBar context={this.formRef} />
					<div ref={this.formRef}>{this.props.render({ ...this.props, cFormMaster })}</div>
				</div>
			);
		} else {
			return <FallbackComponent errors={this.props.errors} loading={this.props.loading} />;
		}
	}
}

const mapDispatchToProps = {
	getPermitAndUserData,
	getFormContainerData,
	postFormDataByUrl,
	getAdminDataByUrl,
	postFormDataWithFileByUrl,
	// setPrint: (res) => setPrint(res),
};

const mapStateToProps = (state: TReduxState) => {
	return {
		permitData: state.root.formData.permitData,
		userData: state.root.formData.userData,
		formData: state.root.formData,
		comment: state.root.formData.comment,
		formHistory: state.root.formData.history,
		loading: state.root.ui.loading,
		errors: state.root.ui.errors,
		success: state.root.formData.success,
		printcontent: state.root.general.printcontent,
		fileCategories: state.root.formData.fileCategories,
		files: state.root.formData.files,
		adminData: state.root.admin,
	};
};

export default connect(mapStateToProps, mapDispatchToProps)(DynamicFormContaner);
