import React, { useEffect, useState } from 'react';
import DynamicFormContaner from '../../../containers/base/DynamicFormContainer';
import api from '../../../utils/api';
import { formNameMasterData } from '../../../utils/data/ebps-setup/tableSetup';
import { ApiParam } from '../../../utils/paramUtil';
import { IFormFieldList, IFormNameMaster } from '../interfaces';
import { LayoutRenderer } from '../layoutSetupComponents/LayoutRenderer';
import { Preview } from '../layoutSetupComponents/Preview';
import { TInputTypeObject, TOutputFormat } from '../layoutSetupComponents/utils/types/setupTypes';

export const DynamicFormComponent = ({ cFormMaster }: { cFormMaster: IFormNameMaster }) => {
	const [output, setOutput] = useState<TOutputFormat[]>([]);
	const [inputTypes, setInputTypes] = useState<TInputTypeObject[]>([]);
	const [readOnlyFields, setReadOnlyFields] = useState<IFormFieldList[]>([]);

	useEffect(() => {
		if (cFormMaster) {
			try {
				const { layout, inputTypes } = JSON.parse(cFormMaster.formLayout);
				setOutput(layout);
				setInputTypes(inputTypes);
			} catch (error) {}
		}
	}, [cFormMaster]);
	return (
		<div>
			Form To Test {cFormMaster && cFormMaster.name}
			<LayoutRenderer
				output={output}
				setOutput={setOutput}
				inputTypes={inputTypes}
				// formattedData={formattedData || ""}
				data={cFormMaster.formLayout}
				dataMapping={readOnlyFields}
			/>
		</div>
	);
};

export const DynamicForm = () => {
	return (
		<DynamicFormContaner
			api={[new ApiParam(api.d_formNameMaster, 'formNameMaster').getParams()]}
			render={
				//@ts-ignore
				(props) => <DynamicFormComponent {...props} />
			}
		/>
	);
};
